import React, { lazy } from 'react'
import NavBar from '../components/NavBar';
import Hero from '../sections/Hero';
import img from '../assets/images/story.webp'
import Team from '../sections/Team';
import img2 from '../assets/images/why-us.webp'
import Partners from '../sections/Partners';
import Works from '../sections/Works';

const Footer = lazy(() => import('../components/Footer'))
const ImgSection = lazy(() => import('../components/ImgSection'))

const Home = () => {
  return (
    <>
    <NavBar/>
      <main>
        <Hero/>
        <ImgSection id='our-story' img={img} title=' ' subtitle=' ' description='Some description text goes here.       -    ,    ,    .             ,        .   -       .           :        ()      .' />

        <Team />
        <ImgSection  id='why-are-we' gradient='linear-gradient(90deg, #490102 0%, #4E0141 100%);' subtitle='' title='  
           ?' description='Some description text goes here.       -    ,    ,    .             ,        .   -       .           :        ()      .' img={img2} />
        <Partners />
        <Works />
      </main>
      <Footer />
    </>
  )
}

export default Home